<template>
  <div>
    <v-expansion-panels dark>
      <v-expansion-panel class="panel" style="background-color: rgb(0, 40, 50)">
        <v-expansion-panel-header
          class="text-h6 d-flex"
          expand-icon="mdi-chevron-down"
        >
          <p class="white--text">E-mail Verification</p>
          <p class="white--text">{{ VerifiedEmail }}</p>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row>
            <v-col cols="12" class="d-block d-md-flex">
              <p class="text-h6 white--text">Your Email:</p>
              <p class="text-h6 white--text px-md-8">{{ Email }}</p>
              <v-btn
                v-show="VerifiedEmail != 'Enabled'"
                color="primary"
                depressed
                :disabled="emailBtnDisabled"
                @click="emailCutdown"
                >{{ emailBtn }}</v-btn
              >
            </v-col>
            <v-col
              v-show="VerifiedEmail != 'Enabled'"
              class="text-h6 white--text d-block d-md-flex"
              cols="12"
            >
              <p class="text-h6 white--text">Verification code:</p>
              <v-text-field
                v-model="emailCode"
                class="px-md-8"
                dark
                single-line
                outlined
                dense
                clearable
                label="Please enter the code"
              ></v-text-field>
              <v-btn
                class="pt-0"
                color="primary"
                depressed
                height="40"
                @click="emailSendCode()"
                >Send code</v-btn
              >
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
      <v-expansion-panel style="background-color: rgb(0, 40, 50)">
        <v-expansion-panel-header
          class="text-h6 d-flex"
          expand-icon="mdi-chevron-down"
        >
          <p class="white--text">Phone Verification</p>
          <p class="white--text">{{ VerifiedPhone }}</p>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row>
            <v-col cols="12" class="d-block d-md-flex">
              <p class="text-h6 white--text">Your Phone:</p>
              <p class="text-h6 white--text px-md-8">{{ Phone }}</p>
              <v-btn
                v-show="VerifiedPhone != 'Enabled'"
                color="primary"
                depressed
                :disabled="phoneBtnDisabled"
                @click="phoneCutdown"
                >{{ phoneBtn }}</v-btn
              >
            </v-col>
            <v-col
              v-show="VerifiedPhone != 'Enabled'"
              class="text-h6 white--text d-block d-md-flex"
              cols="12"
            >
              <p class="text-h6 white--text">Verification code:</p>
              <v-text-field
                v-model="phoneCode"
                dark
                class="px-md-8"
                light
                single-line
                outlined
                dense
                clearable
                label="Please enter the code"
              ></v-text-field>
              <v-btn
                color="primary"
                depressed
                height="40"
                @click="phoneSendCode()"
                >Send code</v-btn
              >
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <div class="text-center">
      <v-dialog class="div-field" v-model="emailModal" hide-overlay persistent>
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            email has been send
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog
        class="div-field"
        v-model="emailSuccessModal"
        hide-overlay
        persistent
      >
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Success
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog
        class="div-field"
        v-model="emailErrorModal"
        hide-overlay
        persistent
      >
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Please enter the correct code
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog class="div-field" v-model="phoneModal" hide-overlay persistent>
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            SMS has been send
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog class="div-field" v-model="phoneSuccessModal" hide-overlay persistent>
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Success
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog class="div-field" v-model="phoneErrorModal" hide-overlay persistent>
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            Please enter the correct code
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import { BindEmail, verifyEmail, BindPhone, verifyPhone } from "../../api";

export default {
  name: "AccountVerify",

  data: () => ({
    emailSecond: 5,
    emailBtn: "Send email",
    emailBtnDisabled: false,
    emailCode: "",
    phoneSecond: 5,
    phoneBtn: "Send Phone",
    phoneBtnDisabled: false,
    phoneCode: "",
    emailModal: false,
    emailSuccessModal: false,
    emailErrorModal: false,
    phoneModal: false,
    phoneSuccessModal: false,
    phoneErrorModal: false,
  }),
  created() {
    this.$store.dispatch("getUserData");
  },
  computed: {
    Email() {
      return this.$store.state.email;
    },
    Phone() {
      return this.$store.state.phone;
    },
    VerifiedEmail() {
      if (this.$store.state.verifiedEmail == 0) {
        return "Not enabled";
      } else if (this.$store.state.verifiedEmail == 2) {
        return "Enabled";
      }
    },
    VerifiedPhone() {
      if (this.$store.state.verifiedPhone == 0) {
        return "Not enabled";
      } else if (this.$store.state.verifiedPhone == 2) {
        return "Enabled";
      }
    },
  },
  methods: {
    //發送信箱驗證信
    emailCutdown() {
      const vm = this;
      const time = setInterval(function () {
        if (vm.emailSecond > 0) {
          vm.emailBtn = vm.emailSecond;
          vm.emailBtnDisabled = true;
          vm.emailSecond--;
        } else {
          vm.emailBtn = "Send email";
          vm.emailBtnDisabled = false;
          vm.emailSecond = 60;

          clearInterval(time);
        }
      }, 1000);
      this.sendEmailApi();
    },

    //送出信箱驗證碼
    emailSendCode() {
      this.sendEmailCode();
    },

    //信箱綁定api
    sendEmailApi() {
      const params = new URLSearchParams();
      params.append("Email", this.Email);

      BindEmail(params)
        .then((result) => {
          if (result.data.success == true) {
            this.emailModal = true;
            setTimeout(() => {
              this.emailModal = false;
            }, 2000);
          } else {
            console.log(result.data.error_msg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //信箱驗證api
    sendEmailCode() {
      const params = new FormData();
      params.append("Email", this.Email);
      params.append("code", this.emailCode);

      verifyEmail(params)
        .then((result) => {
          if (result.data.success == true) {
            this.emailSuccessModal = true;
            setTimeout(() => {
              this.emailSuccessModal = false;
              location.reload();
            }, 2000);
          } else {
            this.emailErrorModal = true;
            setTimeout(() => {
              this.emailErrorModal = false;
            }, 2000);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //發送手機驗證信
    phoneCutdown() {
      const vm = this;
      const time = setInterval(function () {
        if (vm.phoneSecond > 0) {
          vm.phoneBtn = vm.phoneSecond;
          vm.phoneBtnDisabled = true;
          vm.phoneSecond--;
          console.log(vm.phoneSecond);
        } else {
          vm.phoneBtn = "Send email";
          vm.phoneBtnDisabled = false;
          vm.phoneSecond = 5;

          clearInterval(time);
        }
      }, 1000);

      this.sendPhoneApi();
    },

    //送出簡訊
    phoneSendCode() {
      this.sendPhoneCode();
    },

    //發送簡訊api
    sendPhoneApi() {
      const params = new URLSearchParams();
      params.append("Email", this.Email);
      params.append("Mobile_Num", this.Phone);

      BindPhone(params)
        .then((result) => {
          if (result.data.success == true) {
            this.phoneModal = true;
            setTimeout(() => {
              this.phoneModal = false;
            }, 2000);
          } else {
            console.log(result.data.error_msg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //手機驗證api
    sendPhoneCode() {
      const params = new URLSearchParams();
      params.append("Email", this.Email);
      params.append("Mobile_Num", this.Phone);
      params.append("code", this.phoneCode);

      verifyPhone(params)
        .then((result) => {
          console.log(result);
          if (result.data.success == true) {
            this.phoneSuccessModal = true;
            setTimeout(() => {
              this.phoneSuccessModal = false;
              location.reload();
            }, 2000);
          } else {
            this.phoneErrorModal = true;
            setTimeout(() => {
              this.phoneErrorModal = false;
            }, 2000);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
};
</script>

<style lang="css" scoped>
.panel {
  background-color: #002832;
}

.panel i {
  color: white;
}
</style>